.container-prediction{
    display: flex;
}
.container-predict-input{
    width: 50%;
    display: flex;
}
.container-predict-header{
    margin-top: 10px;
    margin-bottom: 40px;
    text-align: center;
}
.container-predict-div-left{
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    float:left;
}
.container-predict-submit{
    float: right;
    padding-right: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.container-predict-submit input{
   background-color: #648381;
   width: fit-content;
   font-family: -apple-system,BlinkMacSystemFont,'Segoe UI',Roboto,'Helvetica Neue',Arial,'Noto Sans',sans-serif,'Apple Color Emoji','Segoe UI Emoji','Segoe UI Symbol','Noto Color Emoji';
   color: white;
   font-size: 14px;
   padding: 5px 20px;
   border-radius: 32px;
   border: 1px solid transparent;
}

.container-predict-submit input[type="submit"]:active {
  background-color: #1677ff;
}

.container-predict-output{
    width: 50%;
    background-image: url('../../images/pic_2.jpg');
    background-size: cover;
    display: flex;
}

.radio-divs{
    padding-left: 20%;
    margin-bottom: 5px;
    float: right;
    font-weight: lighter;
}

.input-divs{
    padding-left: 0px;
    margin-bottom: 2%;
    border-left: 0px;
}
  